.store-list {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /*justify-content: space-between;*/
  /*align-items: stretch;*/
}

.store-list:last-child {
  float: left;
}

/* spinner */
.loading {
  height: 60vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 auto;
  -ms-flex-direction: column;
      flex-direction: column;
}

.loading p {
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
}

.circle {
  margin: 6px;
  width: 11px;
  height: 11px;
  background-color: #637388;
  border-radius: 50%;
/*   animation: bouncing 1.2s ease-in-out infinite; */
}

.circles {
  display: -ms-flexbox;
  display: flex;
}

.circle:nth-child(1) {
  -webkit-animation: bouncing 0.6s ease-in-out 0s infinite;
          animation: bouncing 0.6s ease-in-out 0s infinite;
}

.circle:nth-child(2) {
  -webkit-animation: bouncing 0.6s ease-in-out 0.14s infinite;
          animation: bouncing 0.6s ease-in-out 0.14s infinite;
}

.circle:nth-child(3) {
  -webkit-animation: bouncing 0.6s ease-in-out 0.28s infinite;
          animation: bouncing 0.6s ease-in-out 0.28s infinite;
}

@-webkit-keyframes bouncing {
  0%{
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
  50%{
    -webkit-transform: scale(1.1) translateY(22px);
            transform: scale(1.1) translateY(22px);
  }
  100%{
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
  }
}

@keyframes bouncing {
  0%{
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
  50%{
    -webkit-transform: scale(1.1) translateY(22px);
            transform: scale(1.1) translateY(22px);
  }
  100%{
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
  }
}



