.banner-wrapper {
  background: -webkit-linear-gradient(315deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.1) 50%), url("http://res.cloudinary.com/dsdeorvu4/image/upload/v1509570341/menswhere-front_vkajyj.jpg");
  background: -o-linear-gradient(315deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.1) 50%), url("http://res.cloudinary.com/dsdeorvu4/image/upload/v1509570341/menswhere-front_vkajyj.jpg");
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.1) 50%), url("http://res.cloudinary.com/dsdeorvu4/image/upload/v1509570341/menswhere-front_vkajyj.jpg");
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  background-size: cover;
}

.logo-home {
  color: #f6f7f7;
  text-transform: uppercase;
  font-size: 56px;
  padding: 15px;
  letter-spacing: 2.3px;
}

.banner-content {
  color: #e9ecec;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.33);
  font-size: 25px;
}

.toggle-cities {
  padding: 0 17px;
}



